<template>
    <div class="v-container">
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ name: 'setting' }">设置</el-breadcrumb-item>
            <el-breadcrumb-item>项目管理</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <div class="setting-container">
            <el-row :gutter="30">
                <el-col :span="8" v-for="settingInfo in settingInfos" :key="settingInfo.link">
                    <div class="setting-content">
                        <router-link :to="settingInfo.link">
                            <div class="setting-info">
                                <i :class='[settingInfo.classObj]'></i>
                                <h4>{{settingInfo.title}}</h4>
                            </div>
                        </router-link>
                    </div>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'projectManage',
        data() {
            return {
                settingInfos: [
                    {
                        classObj: 'iconfont icon-xiangmurognqiguanli set-ico',
                        title: "项目容器管理",
                        link: "/setting/projectManage/projectClassManage"
                    },
                    {
                        classObj: 'iconfont icon-xiangmu set-ico',
                        title: "项目信息管理",
                        link: "/setting/projectManage/projectInfoManage"
                    },
                    {
                        classObj: 'iconfont icon-xiangmuzuguanli set-ico',
                        title: "项目组管理",
                        link: "/setting/projectManage/projectTeamManage"
                    },
                    {
                        classObj: 'iconfont icon-jueseguanli set-ico',
                        title: "项目角色管理",
                        link: "/setting/projectManage/projectRoleManage"
                    },
                    {
                        classObj: 'iconfont icon-renyuanquanxian set-ico',
                        title: "项目成员管理",
                        link: "/setting/projectManage/projectMemberManage"
                    },
                    {
                        classObj: 'iconfont icon-xiangmupeizhi set-ico',
                        title: "项目更新配置",
                        link: "/setting/projectManage/projectUpdateConf"
                    },
                    {
                        classObj: 'iconfont icon-xianbgmugengxin set-ico',
                        title: "项目更新",
                        link: "/setting/projectManage/projectUpdate"
                    },
                ]
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-col {
        padding: 20px;
    }

    .setting-container {
        padding: 20px;
    }

    .setting-content {
        height: 150px;
        background: #ffffff;
        text-align: center;
        border-radius: 15px;
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5);
    }

    .setting-info {
        padding-top: 26px;
        cursor: pointer;
    }

    .set-ico {
        padding-top: 26px;
        font-size: 48px;
    }

    /*  .el-breadcrumb{
        font-size: 12px !important;
      }*/
</style>



